import request from '@/utils/request';

export function loginByUsername(username, password, code) {
  var data = {
    email: username,
    password: password,
    code: code
  };
  console.log('bef log: ', data);
  return request({
    url: '/auth',
    method: 'post',
    data: data
  });
}

export function logout() {
  return request({
    url: '/login/logout',
    method: 'post'
  });
}

export function getUserInfo(token) {
  return request({
    url: '/user/info',
    method: 'get',
    params: { token: token }
  });
}