import { getToken } from '@/utils/auth';

export default {
  name: 'App',
  data: function data() {
    return {
      show: true
    };
  },
  mounted: function mounted() {
    this.check(this.$route.meta.logged);
  },


  methods: {
    check: function check(status) {
      this.show = false;
      if (!status) {
        this.show = true;
      } else if (status && getToken()) {
        this.show = true;
      } else {
        this.$router.push('/login');
        this.show = false;
      }
    }
  },

  watch: {
    '$route.meta.logged': function $routeMetaLogged(val) {
      this.check(val);
    }
  }
};