export default {
  props: {
    value: {
      type: String,
      default: 'xlsx'
    }
  },
  data: function data() {
    return {
      options: ['xlsx', 'csv', 'pdf', 'txt']
    };
  },

  computed: {
    bookType: {
      get: function get() {
        return this.value;
      },
      set: function set(val) {
        this.$emit('input', val);
      }
    }
  }
};