import _Promise from 'babel-runtime/core-js/promise';
import axios from 'axios';
import { Message } from 'element-ui';
// import store from '@/store'
import { getToken } from '@/utils/auth';
import store from '../store';
require('dotenv').config();

// create an axios instance
var service = axios.create({
  // baseURL: 'https://api.api-digitalmarket.com/api',
  baseURL: '/api',
  timeout: 9000000 // request timeout
});

// request interceptor
service.interceptors.request.use(function (config) {
  // Do something before request is sent
  if (getToken()) {
    config.headers['Authorization'] = 'Bearer ' + getToken();
  }
  return config;
}, function (error) {
  _Promise.reject(error);
});

// response interceptor
service.interceptors.response.use(function (response) {
  return response;
}, function (error) {
  if (error.response.data.error === 'token_expired' || error.response.data.error === 'token_not_provided') {

    store.dispatch('FedLogOut').then(function () {
      Message.error('Token Expired, please login again');
    });
  }
  return _Promise.reject(error);
});

export default service;